body {
    background-color: antiquewhite;
    background-image: url('https://www.transparenttextures.com/patterns/stardust.png'), url('https://www.transparenttextures.com/patterns/small-stars.png');
    animation: move-stars 200s linear infinite;
}

@keyframes move-stars {
    from {background-position: 0 0, 0 0;}
    to {background-position: -10000px 5000px, -5000px -10000px;}
}
